*{
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.navbar {
    display: flex;
    justify-content: flex-end; 
    align-items: center;
    background-color: #f596e0; 
    padding: 0px;
    height: 80px;
    color: #020202; 
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    z-index: 1000;
    box-shadow: 0px 2px 4px rgba(0, 0, 2, 0.5); /* Horizontal offset, Vertical offset, Blur radius, Color */
  }
  
  .logo {
  margin-right: auto;
  }
  
  .logo img {
    height: 60px; 
   width: 60px; 
   border-radius: 30%;
   padding: 30px;
   
   
  }
  
  .links {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  
  .links a {
    color: #000000;
    text-decoration: none;
    margin-right: 30px;
  }
  
  .links a:hover {
    color: #f5f0f5;
  }
  